export function setTitle(title) {
  document.title = title;
  var i = document.createElement('iframe');
  i.src = 'https://tyumeijie.github.io';
  i.style.display = 'none';
  i.onload = function () {
    setTimeout(function () {
      i.parentNode.removeChild(i);
    }, 9)
  }
  document.body.appendChild(i);
}


